import React, { Component } from "react";
import Footer from "./Footer";
import TestImage from "../Images/contact3.jpg";
import Cards from "./Cards2";
import "./CardProduits2.css";
import "./Ateliers.css";
//import
import IMG1 from "../Images/LMDA1.jpg";
import IMG2 from "../Images/LMDA2.jpg";
import IMG3 from "../Images/LMDA3.png";
import IMG4 from "../Images/LMDA4.png";
import IMG5 from "../Images/LMDA5.png";

//

const listPictureAteliers = [
  {
    images: IMG1,
    title: "GAULETTE",
    describe:
      "Atelier de cration, production dobjets dameublement et de dcoration  base de gaulette* (*arbre merisier, bois dinde, bois chandelle) : tables basses, des lampes, des objets dcorations ..."
  },
  {
    images: IMG2,
    title: "ACCESSOIRISATION DE VETEMENT",
    describe:
      "Atelier daccessoirisation. Les boubous croles sont raliss par notre partenaire, et accessoiriss avec des graines naturelles, des coquillages, ou autres lments naturels."
  },
  {
    images: IMG3,
    title: "BIJOUX ET ACCESSOIRES DE MODE",
    describe:
      "Atelier de cration, production de bijoux et accessoires de mode  base de graines naturelles (il de buf et de bourrique, savonette, graine de job, canique...), de coquillages et coraux et autres lments naturels (dents de cochon, bambou...)..."
  },
  {
    images: IMG4,
    title: "ACCESSOIRES ET OBJETS DECORATIFS",
    describe:
      "Atelier de cration, production daccessoires et objets dcoratifs  base de calebasses, bois flott, graines, sables et autres lments naturels. A savoir : kwi, lampes, prsentoir boucle doreille..."
  },
  {
    images: IMG5,
    title: "ATELIERS POUR ENFANTS",
    describe:
      "LA MAISON DES ARTISTES se veut tre aussi, un ple danimation en ralisant des ateliers pdagogiques  lattention des coles maternelles et primaires qui permettront une sensibilisation des tout petits,  notre environnement de part la richesse quil nous procure."
  }
];

const products = [
  {
    url: "./images/1.jpg",
    nom: "Collier Coquillage"
  },
  {
    url: "images/2.jpg",
    nom: "Collier Dent"
  },
  {
    url: "images/3.jpg",
    nom: "Oeil de boeuf"
  },
  {
    url: "images/4.jpg",
    nom: "Boucles d'oreilles"
  },
  {
    url: "images/5.jpg",
    nom: "Colliers divers"
  },
  {
    url: "images/7.jpg",
    nom: "Colliers de bambou"
  },
  {
    url: "images/8.jpg",
    nom: "Boucles d'oreille avec canique"
  },
  {
    url: "images/6.jpg",
    nom: "Boucles d'oreilles divers"
  }
];
class App extends Component {
  state = {
    modalVisibility: false,
    position: 0,
    nbr: 0
  };
  // function Next and previous

  previousSlide = () => {
    const { position } = this.state;
    this.setState({
      position: position - 1
    });
    if (position <= 0)
      this.setState({
        position: listPictureAteliers.length - 1
      });
  };

  nextSlide = () => {
    this.setState({
      position: this.state.position + 1
    });
    if (this.state.position > listPictureAteliers.length - 2) {
      this.setState({
        position: 0
      });
    }
  };

  showModal = i => {
    this.setState({
      modalVisibility: true,
      nbr: i
    });
  };
  hideModal = () => {
    this.setState({
      modalVisibility: false
    });
  };

  render() {
    const { modalVisibility } = this.state;
    const newMargin = this.state.position * -275;
    const newMargins = this.state.position * -470;
    return (
      <div id="App">
        <strong>
          <p className="TextLoremH">
            Marie-galante est riche de productions locales et de traditions
            ancres dans le patrimoine. Par consquent LA MAISON DES ARTISTES se
            fera un plaisir de contribuer  leur valorisation ainsi qu leur
            promotion,  travers toutes nos crations.
          </p>
        </strong>
        <div className="DivOfTitle2">
          <p className="TextOfDivOfTitle2">ATELIERS</p>
        </div>

        {/*PARTIE SLIDER ATELIER*/}

        <div className="ateslider">
          {/*PARTIE TITLE ATELIER*/}

          <div className="boxtitle">
            <ul style={{ marginLeft: newMargin }} className="list">
              {listPictureAteliers.map((slide, i) => {
                return (
                  <li key={i}>
                    <p className="atesentences">{slide.title}</p>
                  </li>
                );
              })}
            </ul>
          </div>

          {/*PARTIE IMAGES ATELIER*/}

          <div className="box">
            <a onClick={this.previousSlide} type="button">
              <i className="fas fa-chevron-left" />
            </a>
            <div className="slider-container">
              <ul style={{ marginLeft: newMargin }} className="list">
                {listPictureAteliers.map((el, i) => {
                  return (
                    <li key={i}>
                      <img
                        className="atepicture"
                        src={el.images}
                        alt={el.title}
                      />
                    </li>
                  );
                })}
              </ul>
            </div>
            <a onClick={this.nextSlide} type="button">
              <i className="fas fa-chevron-right" onClick={this.nextSlide} />
            </a>
          </div>

          {/*
  LA PARTIE  DESCRIPTION ATELIER

*/}

          <div className="boxsentence">
            <ul style={{ marginLeft: newMargins }} className="list">
              {listPictureAteliers.map((slide, i) => {
                return (
                  <li key={i}>
                    <p className="atesentence">{slide.describe}</p>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>

        {/*

Partie a  ne pas toucher

*/}

        {modalVisibility ? (
          <div id="modal-container" onClick={this.hideModal}>
            <div
              id="creatorprofile"
              onClick={
                e => {
                  e.stopPropagation();
                } // Do something
              }
            >
              <img
                className="creatorpp"
                src={products[this.state.nbr].url}
                alt={products[this.state.nbr].nom}
              />
            </div>
            <img
              alt=""
              id="croix"
              onClick={this.hideModal}
              src="https://smartbees.fr/assets/img/x-min.png"
            />
          </div>
        ) : null}

        {/* liste des produits */}
        {modalVisibility ? (
          <div id="modal-container" onClick={this.hideModal}>
            <div
              id="creatorprofile"
              onClick={
                e => {
                  e.stopPropagation();
                } // Do something
              }
            >
              <img
                className="creatorpp"
                src={products[this.state.nbr].url}
                alt={products[this.state.nbr].nom}
              />
            </div>
            <img
              alt=""
              id="croix"
              onClick={this.hideModal}
              src="https://smartbees.fr/assets/img/x-min.png"
            />
          </div>
        ) : null}
        <div className="DivOfTitle2">
          <p className="TextOfDivOfTitle2">PRODUITS</p>
        </div>
        <ul id="Cards-container">
          {products.map((Card, i) => {
            return (
              <li className="Cards" onClick={() => this.showModal(i)} key={i}>
                <img className="creatorspp" src={Card.url} alt={Card.nom} />
                <p>{Card.nom}</p>
              </li>
            );
          })}
        </ul>
        <Footer />
      </div>
    );
  }
}

export default App;



